import React, { useState } from "react";
import apiClient from "../services/api";
import { useTranslation } from "react-i18next";
import TextField from "@mui/material/TextField";
import Button from "@mui/material/Button";
import Stack from "@mui/material/Stack";
import Alert from "@mui/material/Alert";
import MenuItem from "@mui/material/MenuItem";
import FileUpload from "./FileUpload"; // Importar el nuevo componente

function CreateOffer({ onOfferCreated, onClose }) {
  const { t } = useTranslation();
  const [formData, setFormData] = useState({
    titulo: "",
    descripcion: "",
    ubicacion: "",
    puesto: "",
    salario: "",
    nivel: "",
    horarios: "",
    detalles_adicionales: "",
  });
  const [files, setFiles] = useState([]);
  const [error, setError] = useState("");
  const [success, setSuccess] = useState("");
  const [uploadProgress, setUploadProgress] = useState(0);
  const [isSubmitting, setIsSubmitting] = useState(false);

  const handleChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError("");
    setSuccess("");
    setUploadProgress(0);
    setIsSubmitting(true);

    const data = new FormData();
    Object.keys(formData).forEach((key) => {
      data.append(key, formData[key]);
    });

    // El nombre del campo debe coincidir con el backend (Multer)
    if (files.length > 0) {
        data.append('imagen_url', files[0]);
    }
    // Si el backend aceptara más imágenes, se añadirían aquí:
    // files.forEach((file, index) => {
    //   data.append(`imagen_${index + 1}`, file);
    // });

    const config = {
      onUploadProgress: (progressEvent) => {
        const percentCompleted = Math.round(
          (progressEvent.loaded * 100) / progressEvent.total
        );
        setUploadProgress(percentCompleted);
      },
    };

    try {
      await apiClient.post("/offers", data, config);
      setSuccess(t("offer_published_success"));
      // Resetear formulario
      setFormData({ titulo: "", descripcion: "", ubicacion: "", puesto: "", salario: "", nivel: "", horarios: "", detalles_adicionales: "" });
      setFiles([]);
      if (onOfferCreated) {
        onOfferCreated();
      }
      setTimeout(() => {
        if (onClose) onClose();
        setSuccess("");
      }, 2000);
    } catch (err) {
      setError(err.response?.data?.message || t("error_publishing_offer"));
    } finally {
      setIsSubmitting(false);
      // Resetear progreso después de un momento
      setTimeout(() => setUploadProgress(0), 1500);
    }
  };

  return (
    <div className="form-card create-offer-card">
      <h2>{t("create_offer_title")}</h2>
      {success && <Alert severity="success" sx={{ mt: 2 }}>{success}</Alert>}
      {error && <Alert severity="error" sx={{ mt: 2 }}>{error}</Alert>}
      
      <form onSubmit={handleSubmit}>
        <Stack spacing={2} sx={{ mt: 2 }}>
          <TextField name="titulo" label={t("offer_title_placeholder")} value={formData.titulo} onChange={handleChange} required fullWidth />
          <TextField name="descripcion" label={t("offer_description_placeholder")} value={formData.descripcion} onChange={handleChange} required fullWidth multiline rows={4} />
          <TextField name="ubicacion" label={t("location_placeholder")} value={formData.ubicacion} onChange={handleChange} fullWidth />
          <TextField name="puesto" label={t("position_placeholder")} value={formData.puesto} onChange={handleChange} fullWidth />
          <TextField name="salario" label={t("salary_placeholder")} value={formData.salario} onChange={handleChange} fullWidth />
          <TextField select name="nivel" label={t("select_level_placeholder")} value={formData.nivel} onChange={handleChange} fullWidth>
            <MenuItem value="">{t("select_level_placeholder")}</MenuItem>
            <MenuItem value="Profesional">{t("level_professional")}</MenuItem>
            <MenuItem value="Semi-profesional">{t("level_semi_professional")}</MenuItem>
            <MenuItem value="Amateur">{t("level_amateur")}</MenuItem>
          </TextField>
          <TextField name="horarios" label={t("schedule_placeholder")} value={formData.horarios} onChange={handleChange} fullWidth />
          <TextField name="detalles_adicionales" label={t("additional_details_placeholder")} value={formData.detalles_adicionales} onChange={handleChange} fullWidth multiline rows={3} />
          
          {/* --- Nuevo Componente de Subida de Archivos --- */}
          <FileUpload onFilesChange={setFiles} uploadProgress={uploadProgress} />

          <Stack direction="row" spacing={2} justifyContent="flex-end" sx={{ pt: 2 }}>
            <Button variant="contained" color="primary" type="submit" disabled={isSubmitting}>
              {isSubmitting ? t("publishing...") : t("publish_offer_button")}
            </Button>
            {onClose && (
              <Button variant="outlined" color="secondary" onClick={onClose} type="button" disabled={isSubmitting}>
                {t("cancel_button")}
              </Button>
            )}
          </Stack>
        </Stack>
      </form>
    </div>
  );
}

export default CreateOffer;